import React from "react";
import { Passo1, Passo2, Passo3 } from ".";

export default class MasterPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            passo: 1,

            // States Passo 1:
            nome: "",
            dataNascimento: "",
            idade: "40",
            idadeDecimal: "",
            tipoAtivo: "normal",
            idadeAposentadoria: "40",
            tempoAposentadoria: "20",
            tempoSobrevivencia: "5",
            remuneracaoInicial: "",
            remuneracaoFinal: "",
            taxaCrescimentoSalarial: "2.00",
            taxaJurosReal: "4.5",
            termosAceitos: false,
            rgps: 0,
            
            // States validao 
            erroNome: false,
            erroDataInvalida: false,
            erroRemuneracaoInicialInvalida: false,
            erroRemuneracaoFinal: false,
            erroRemuneracaoFinalInvalida: false,
            temSobrevivencia: true,

            // States Passo 2:
            // Contribuies Participante
            percentualBasicoParticipante: "3.00",
            beneficioRiscoInvalidezParticipante: "0.00",
            beneficioRiscoPensaoMorteParticipante: "0.00",
            sobrevivenciaParticipante: "0.00",
            carregamentoContribuicaoBasicaParticipante: "0.18",     // Valor mnimo que o carregamento contribuio bsica do participante pode ter, pois o menor valor do Percentual bsico  3 e o carregamento  (Percentual Bsico * 0.06).

            // Contribuies Patrocinador
            percentualBasicoPatrocinador: "0.00",
            beneficioRiscoInvalidezPatrocinador: "0.00",
            beneficioRiscoPensaoMortePatrocinador: "0.00",
            sobrevivenciaPatrocinador: "0.00",
            carregamentoContribuicaoBasicaPatrocinador: "0.00",

            // Contribuies Adicionais
            aposentadoriaAdicional: "0",
            invalidezAdicional: "0",
            pensaoMorteAdicional: "0",
            sobrevivenciaAdicional: "0",
            carregamentoContribuicaoAdicional: "0.00",
            totalizadorTaxaAposentadoria: "0.00",

            // States de validao por Soma e valor Mx.
            beneficioRiscoInvalidezMax: "1.00",
            beneficioRiscoPensaoMorteMax: "1.00",
            beneficioRiscoSoma: "0",
            contribuicaoSobrevivenciaDesabilitada: true,

        };

        this.setPassoAtivo = this.setPassoAtivo.bind(this);
        this.renderAbaPassos = this.renderAbaPassos.bind(this);
        this.renderPassoBotao = this.renderPassoBotao.bind(this);
        this.renderConteudoPassos = this.renderConteudoPassos.bind(this);

        this.passo1 = React.createRef();
        this.passo2 = React.createRef();
        this.passo3 = React.createRef();
    }

    /**
     * @description Mtodo de ciclo de vida do React.  chamado apenas uma vez, antes do render.
     */
    componentWillMount() {
        window.scrollTo(0, 0);
    }

    /**
     * @param {Object} event Evento enviado pelo componente.
     * @description Mtodo que atualiza o state a partir do nome e valor do campo que disparou o evento.
     */
    handleInputChange(event) {
        const target = event.target;

        // Caso o input seja radio (Tipo de ativo), seu valor ser true para 'Ativo'. Caso o input seja normal, o valor  o valor de entrada trocando vrgula por ponto, para realizao dos clculos.
        var value;
        if(target.type === "radio")
            value = target.value;
        else
            value = target.value.replace(',', '.');

        const name = target.name;

        // var self = this;
        this.setState({
            [name]: value
        });
    }

    /**
     * @param {int} passo Passo do simulador, de 1 a 3.
     * @description Mtodo que 'setta' o state do passo recebido para mudar o passo do simulador.
     */
    setPassoAtivo(passo, state) {
        window.scrollTo(0, 0);

        this.setState(state, () => {
            this.setState({
                passo: passo
            }, () => {
                
                switch(passo)
                {
                    case 1:
                        this.passo1.current.onVisible(this.state);
                        break;
                    case 2:
                        this.passo2.current.onVisible(this.state);
                        break;
                    case 3:
                        this.passo3.current.onVisible(this.state);
                        break;
                    default:
                        break;
                }
            });
        });
    }

    /**
     * @description Mtodo que atualiza o state modalVisivel, que fica com o valor 'true' quando a modal estiver aberta.
     */
    toggleModal() {
        this.setState({ modalVisivel: !this.state.modalVisivel })
    }

    /**
     * @param {int} id Id do boto a ser renderizado, de 1 a 3. 
     * @param {string} titulo Ttulo principal do boto.
     * @param {string} subtitulo Subttulo do boto, texto abaixo do ttulo.
     * @description Mtodo responsvel pela renderizao e comportamento dos botes de passo. Esses botes alteram sua cor de acordo com o passo ativo e ficam desativados caso o passo ativo seja menor que o id do boto.
     */
    renderPassoBotao(id, titulo, subtitulo) {
        return (
            <button className={"btn " + estadoPasso(id, this.state.passo)} disabled={this.state.passo < id}
                    onClick={() => this.setPassoAtivo(id)}> 
                <span>{titulo}</span>
                <h3>{subtitulo}</h3>
            </button>
        )
    }

    /**
     * @description Mtodo que renderiza a aba de passos com os botes de passos. So 3 botes que renderizam 3 passos diferentes. 
     */
    renderAbaPassos() {
        return(
            <div id="passos">
                {this.renderPassoBotao(1, "Passo 1", "Dados Bsicos")}
                {this.renderPassoBotao(2, "Passo 2", "Contribuio")}
                {this.renderPassoBotao(3, "Passo 3", "Resultado")}
            </div>
        );
    }

    /**
     * @description Mtodo responsvel por renderizar todo o contedo dos passos. Nele existem 3 componentes: Passo1, Passo2 e Passo3, que contm o contedo de cada passo, mas esto visveis apenas se o passo atual for o mesmo do passo informado no componente, pelo atributo 'hidden'.
     */
    renderConteudoPassos() {
        return (
            <div>
                <Passo1 setPassoAtivo={this.setPassoAtivo} state={this.state} handleInputChange={this.handleInputChange}
                        hidden={this.state.passo !== 1} ref={this.passo1} />
                <Passo2 setPassoAtivo={this.setPassoAtivo} state={this.state} handleInputChange={this.handleInputChange}
                        hidden={this.state.passo !== 2} ref={this.passo2} />
                <Passo3 setPassoAtivo={this.setPassoAtivo} state={this.state} handleInputChange={this.handleInputChange}
                        hidden={this.state.passo !== 3} ref={this.passo3} />
            </div>
        );
    }

    render() {
        return (
            <div className="container">
                <div align="center">
                    <img className="logo figure-img" src="img/logo.png" alt="" />
                    <h2>Bem vindo ao Simulador de Benefcio</h2>

                    {this.renderAbaPassos()}
                </div>

                {this.renderConteudoPassos()}
            </div>
        );
    }
}

/**
 * @param {int} id Id do boto, entre 1 e 3.
 * @param {int} passoAtivo Passo atual. Esse valor  obtido do state 'passo'.
 * @description Funo que retorna uma string com a classe de botes do bootstrap. Caso o passo ativo seja igual ao id do boto, sua cor ser 'btn-secondary' (rosa). Se o passo atual for maior que o id do boto, esse boto fica da cor 'btn-primary' (verde). E se o id for maior que o passo ativo, esse boto fica claro e desativado.
 */
function estadoPasso(id, passoAtivo) {
    if(passoAtivo === id)
        return "btn-secondary";
    else if(passoAtivo > id)
        return "btn-primary";
    else
        return "btn-light";
}



